import axios from 'axios';
import Vue from 'vue';
import Vuex from 'vuex';
import articles from './assets/data.js';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state:{
        articles : articles,
        more: {},
        count: 0,
    },
    mutations:{
        like(state, idx){
            if(!state.articles[idx].liked){
                state.articles[idx].likes++;
                state.articles[idx].liked = true;
            }else{
                state.articles[idx].likes--;
                state.articles[idx].liked = false;
            }
            
        },
        setMore(state, data){
            state.articles.push(data);
            state.count++;
        },
        addArticle(state, data){
            state.articles.unshift(data);
        }
    },
    actions:{
        getData(context){
            axios
        .get(`https://codingapple1.github.io/vue/more${context.state.count}.json`)
        .then((res) => {
          context.commit('setMore', res.data)
        })
        .catch((err) => {
          console.log(err);
        });
        },
    }
})