<template>
  <div>
    <div v-if="step == 0">
      <Post
        v-for="(article, i) in articles"
        :key="i"
        :article="article"
        :step="step"
        :idx="i"
      />
    </div>

    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div
        class="upload-image"
        :class="filter"
        :style="{ 'background-image': `url(${imgUrl})` }"
      ></div>
      <div class="filters" v-if="step == 1">
        <FilterBox
          v-for="filter in filters"
          :key="filter"
          :imgUrl="imgUrl"
          :filter="filter"
          @filtering="filtering"
          >{{ filter }}</FilterBox
        >
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div
        class="upload-image"
        :class="filter"
        :style="{ 'background-image': `url(${imgUrl})` }"
      ></div>
      <div class="write">
        <textarea
          class="write-box"
          @input="$emit('write', $event.target.value)"
        >
write!</textarea
        >
      </div>
    </div>
  </div>
</template>

<script>
import Post from "./Post.vue";
import FilterBox from "./FilterBox.vue";
import filters from "../assets/filters.js";
import { mapState } from "vuex";

export default {
  //eslint-disable-next-line
  name: "Container",
  components: {
    Post,
    FilterBox,
  },
  props: {
    step: Number,
    imgUrl: String,
  },
  computed: {
    ...mapState(["articles"]),
  },
  data() {
    return {
      content: "",
      filters: filters,
      filter: "",
    };
  },
  methods: {
    filtering(filter) {
      this.filter = filter;
      this.$emit("filtering", filter);
    },
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>
