<template>
  <div class="post">
    <div class="post-header">
      <div
        class="profile"
        :style="{
          'background-image': `url(${article.userImage})`,
        }"
      ></div>
      <span class="profile-name">{{ article.name }}</span>
    </div>
    <div
      class="post-body"
      :class="article.filter"
      :style="{
        'background-image': `url(${article.postImage})`,
      }"
      @dblclick="$store.commit('like', idx)"
    ></div>
    <div class="post-content">
      <p>
        {{ article.likes }} Likes <span v-if="!article.liked">♡</span>
        <span v-else>♥</span>
      </p>
      <p>
        <strong>{{ article.name }}</strong> {{ article.content }}
      </p>
      <p class="date">{{ article.date }}</p>
    </div>
  </div>
</template>

<script>
export default {
  //eslint-disable-next-line
  name: "Post",
  props: {
    article: Object,
    step: Number,
    idx: Number,
  },
};
</script>

<style>
.post {
  width: 100%;
}
.profile {
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
.post-body {
  height: 450px;
  background-position: center;
  background-size: cover;
}
.post-content {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
}
.date {
  font-size: 11px;
  color: grey;
  margin-top: -8px;
}
</style>
